<template>
  <div class="mask">
    <div class="popup">
      <div class="header">
        <span class="title">添加标签</span><span class="close" @click="closePopup">&#xe64e;</span>
      </div>
      <div class="main">
        <p>
          <span class="label">标签名称：</span><input type="text" class="input-text">
        </p>
        <p>
          <span class="label">排 序：</span><input type="text" class="input-text">
        </p>
        <p>
          <span class="label">显示状态：</span><input type="text" class="input-text">
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CDetail',
  methods: {
    closePopup () {
      this.$emit('close', false)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .mask
    mask()
    h-v-center()
    top 1.04rem
    .popup
      width 11.2rem
      height 6.12rem
      background #fff
      .header
        popup-header-footer()
        .title
          float left
          text-indent .7rem
        .close
          font-family 'iconfont'
          padding-right .42rem
      .main
        h-v-center()
        flex-direction column
        height 4rem
        p
          line-height 1rem
          .label
            display inline-block
            width 1.4rem
            text-align right
          .input-text
            width 6rem
            border 1px solid #a9a9a9
            padding-left .1rem
      .footer
        popup-header-footer()
        padding-right .2rem
        .cancel
        .save
          list-button()
          margin-right .58rem
</style>



