<template>
  <div class="desks">
    <div :class="desk_class" @click="open_desk_detail('A01')">
      <div class="desk-id">A01</div>
      <div class="action">
        <div class="add" @click.self.stop="desk_action(0)"></div>
        <div class="pay" @click.self.stop="desk_action(1)"></div>
        <div class="change" @click.self.stop="desk_action(2)"></div>
      </div>
    </div>
    <div :class="desk_class">
      <div class="desk-id">A01</div>
      <div class="action">
        <div class="add"></div>
        <div class="pay"></div>
        <div class="change"></div>
      </div>
    </div>
    <div :class="desk_class">
      <div class="desk-id">A01</div>
      <div class="action">
        <div class="add"></div>
        <div class="pay"></div>
        <div class="change"></div>
      </div>
    </div>
    <div class="add-desk" @click="open_add_desk_popup"></div>
    <div class="mask" v-show="add_desk_popup_seen">
      <div class="popup">
        <div class="header">
          <span class="title">新增桌位</span><span class="close" @click="close_add_desk_popup">&#xe64e;</span>
        </div>
        <div class="main">
          <div class="category">
            <div class="label">
              <p>桌位号:</p>
              <p>排 序:</p>
              <p>备 注:</p>
            </div>
            <div class="input">
              <p><input type="text"></p>
              <p><input type="text"></p>
              <p><input type="text"></p>
            </div>
          </div>
        </div>
        <div class="footer">
          <p><span class="cancel" @click="close_add_desk_popup">取消</span><span class="save">保存</span></p>
        </div>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'Room',
  data () {
    return {
      desk_status: 1, // 0 橙色 1 黄色 2 绿色
      desks: [{
        'desk-id': 'A01',
        desk_status: 0
      }
      ],
      add_desk_popup_seen: false
    }
  },
  computed: {
    desk_class () {
      const classes = [false, false, false]
      classes[this.desk_status] = true
      return {
        'desk': true,
        'chengse': classes[0],
        'huangse': classes[1],
        'lvse': classes[2]
      }
    }
  },
  methods: {
    open_add_desk_popup () {
      this.add_desk_popup_seen = true
    },
    close_add_desk_popup () {
      this.add_desk_popup_seen = false
    },
    open_desk_detail (deskId) {
      this.$emit('open_desk_detail', deskId)
    },
    desk_action (action) { // 0 加菜 1 结算 2 换桌
      // alert(action)
      this.$emit('desk_action', action)
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/viewpadding.styl'
  .desks
    display flex
    flex-wrap wrap
    align-content flex-start
    width 100%
    height 20rem
    .add-desk
      desk-h-w()
      background url('~img/xinzeng.png') 0 0 no-repeat
      background-size cover
    .desk
      desk-h-w()
      background url('~img/chengse.png') 0 0 no-repeat
      .desk-id
        width 0.8rem
        height 0.8rem
        text-align center
        color #000
        position absolute
        left 1.3rem
        top 0.87rem
        font-size 0.4rem
      .action
        display none
    .lvse
      background url('~img/lvse.png') 0 0 no-repeat
      background-size cover
    .chengse
      background url('~img/chengse.png') 0 0 no-repeat
      background-size cover
    .huangse
      background url('~img/huangse.png') 0 0 no-repeat
      background-size cover
    .desk:hover
      .action
        display block
      .action
        .add,
        .pay,
        .change
          width 0.64rem
          height 0.64rem
          position absolute
          // background url('~img/jiacai.png') #ff0 0 0 no-repeat
        .add
          left 3.1rem
          top -0.2rem
          background url('~img/jiacai.png') 0 0 no-repeat
          background-size cover
        .pay
          left 3.36rem
          top .8rem
          background url('~img/jiesuan.png') 0 0 no-repeat
          background-size cover
        .change
          left 3.1rem
          top 1.9rem
          background url('~img/huanzhuo.png') 0 0 no-repeat
          background-size cover
    .mask
      mask()
      h-v-center()
      top 1.04rem
      .popup
        width 11.2rem
        height 6.7rem
        background #fff
        .header
          popup-header-footer()
          .title
            float left
            text-indent .7rem
          .close
            font-family 'iconfont'
            padding-right .42rem
        .main
          h-v-center()
          height 4.7rem
          .category
            width 80%
            height 100%
            display flex
            .label
              width 30%
              height 100%
              p-h-v-center()
              p
                text-align right
                line-height 1.2rem
                padding-right .46rem
            .input
              width 68%
              height 100%
              p-h-v-center()
              p
                line-height 1.2rem
                input
                  width 4rem
                  border 1px solid #a6a6a6
        .footer
          popup-header-footer()
          padding-right .2rem
          .cancel
          .save
            list-button()
            margin-right .58rem
</style>



