<template>
  <div class="view">
    <div class="view-container">
      <room v-show="room_seen"
        @open_desk_detail="handle_open_desk_detail"
        @desk_action="handle_desk_action"
      />
      <desk-detail
        v-show="desk_detail_seen"
        :jiacai="jiacai"
        :jiesuan="jiesuan"
        :huanzhuo="huanzhuo"
      />
    </div>
  </div>
</template>

<script>
import Room from '@/pages/rooms/desks/Room'
import DeskDetail from '@/pages/rooms/detail/DeskDetail'

export default {
  name: 'DinnerRoom',
  components: {
    Room,
    DeskDetail
  },
  data () {
    return {
      pop_seen: false,
      type: 0, // 0 全部 1 待付款 2 交易完成
      // tab_active: [true, false, false]
      room_seen: true,
      desk_detail_seen: false,
      desk_id: '',
      jiacai: false,
      jiesuan: false,
      huanzhuo: false
    }
  },
  computed: {
    tab_active () {
      const active = [true, false, false]
      if (this.type === 0) {
        return active
      } else if (this.type === 1) {
        active.fill(false)
        active[this.type] = true
        return active
      } else {
        active.fill(false)
        active[this.type] = true
        return active
      }
    }
  },
  methods: {
    handleChangeNum (seen) {
      this.pop_seen = seen
    },
    handleTabSwitch (type) {
      this.type = type
    },
    handle_open_desk_detail (deskId) {
      this.room_seen = false
      this.desk_detail_seen = true
      this.desk_id = deskId
    },
    handle_desk_action (action) {
      this.jiacai = false
      this.jiesuan = false
      this.huanzhuo = false
      this.room_seen = false
      this.desk_detail_seen = true
      if (action === 0) {
        this.jiacai = true
      } else if (action === 1) {
        this.jiesuan = true
      } else {
        this.huanzhuo = true
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .view
    min-width 5.6rem
    pad1()
    .view-container
      pad2()
      min-height 50vw
      position relative
      box-sizing border-box

</style>



