<template>
  <div>
    <c-header :title="title" :options1="options1" :options2="options2" />
    <div class="container">
       <i-echarts :option="option"></i-echarts>
    </div>
  </div>
</template>

<script>
import CHeader from '../commons/Header'
import IEcharts from '../commons/I-Echarts'
export default {
  name: 'ItemNumber',
  components: {
    CHeader,
    IEcharts
  },
  data () {
    return {
      title: '单品排行（单品下单量）',
      options1: ['按年统计', '按月统计'],
      options2: [2018, 2017, 2016, 2015, 2014],
      option: {
        color: ['#5793f3', '#d14a61', '#675bba'],
        tooltip: {
          trigger: 'axis'
        },
        grid: {
          bottom: '10%'
        },
        legend: {
          data: ['下单数', '下单总金额(元)']
        },
        xAxis: [
          {
            type: 'category',
            axisLabel: {
              rotate: -90,
              interval: 0
            },
            axisTick: {
              alignWithLabel: true
            },
            data: ['西红柿炒蛋', '鱼香肉丝', '红烧排骨', '酱香驴肉', '麻婆豆腐', '红烧排骨', '蛋炒饼', '豆角焖面', '皮蛋瘦肉粥', '鱼香茄子煲', '狗不理包子', '云吞面']
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '',
            position: 'left',
            axisLabel: {
              formatter: '{value}'
            }
          }
        ],
        series: [
          {
            name: '下单数',
            type: 'bar',
            data: [20, 49, 70, 232, 256, 267, 136, 322, 326, 200, 64, 33]
          },
          {
            name: '下单总金额(元)',
            type: 'bar',
            data: [26, 59, 90, 264, 287, 307, 356, 422, 487, 188, 60, 23]
          }
        ]
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  .container
    width 100%
    height 300px
    background #eee
    display flex
    padding-bottom 0.8rem
    margin-bottom 0.8rem
    .top5
      width 50%
      height 100%
      background #ff00ff
    .bottom5
      width 50%
      height 100%
      background #ffff00
</style>



