<template>
  <div class="view">
    <div class="view-container">
      <today-statistics />
      <history-statistics />
    </div>
  </div>
</template>

<script>
import TodayStatistics from './statistics/TodayStatistics'
import HistoryStatistics from './statistics/HistoryStatistics'

export default {
  name: 'statistics',
  components: {
    TodayStatistics,
    HistoryStatistics
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .view
    min-width 5.6rem
    pad1()
    .view-container
      pad2()
      min-height 50vw
      position relative
      box-sizing border-box
      .tab
        width 6.8rem
        height 1rem
        line-height 1rem
        font-size .36rem
        display flex
        justify-content space-between
        span
          width 1.8rem
          text-align center
        .tab-active
          border-bottom .1rem solid #ff4c1b
          color #ff4c1b
</style>



