<template>
  <div class="table-wrapper">
    <table class="table">
      <thead>
        <tr class="title">
          <th>序号</th>
          <th>订单编号</th>
          <th>日期</th>
          <th>发票金额 (元)</th>
          <th>发票类别</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>1</td>
          <td>20180304000001</td>
          <td>2018-03-01</td>
          <td>¥15.00 </td>
          <td>单位</td>
          <td><span class="change-btn" @click="handleClick">查看详情</span></td>
        </tr>
        <tr>
          <td>2</td>
          <td>20180301000002</td>
          <td>2018-01-16</td>
          <td>¥18.00</td>
          <td>个人</td>
          <td><span class="change-btn" @click="handleClick">查看详情</span></td>
        </tr>
        <tr>
          <td>1</td>
          <td>20180304000001</td>
          <td>2018-03-01</td>
          <td>¥15.00 </td>
          <td>单位</td>
          <td><span class="change-btn" @click="handleClick">查看详情</span></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>

export default {
  name: 'CTable',
  methods: {
    handleClick () {
      this.$emit('change', true)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .table-wrapper
    .table
      width 100%
      margin-bottom 1.6rem
      .title
        height .62rem
        line-height .62rem
        background #f2f2f2
        border 1px solid #f2f2f2
      th
        text-align center
      td
        text-align center
        padding .2rem
        border 1px solid #e4e4e4
        img
          margin-right .2rem
        .change-btn
          list-button()
          margin-left .32rem
          background #38a8fe
</style>



