  data () {
    return {
      qr_popup_seen: false
      // jia_cai_popup_seen: false,
      // c_desk_popup_seen: false,
      // jiesuan_popup_seen: false
    }
  },
  computed: {
    jia_cai_popup_seen: {
      get: function () {
        if (this.jiacai) {
          return true
        } else {
          return false
        }
      },
      set: function (seen) {
        return seen
      }
    },
    c_desk_popup_seen: {
      get: function () {
        if (this.jiesuan) {
          return true
        } else {
          return false
        }
      },
      set: function (seen) {
        return seen
      }
    },
    jiesuan_popup_seen: {
      get: function () {
        if (this.h) {
          return true
        } else {
          return false
        }
      },
      set: function (seen) {
        return seen
      }
    }
  },
  methods: {
    printQR () {
      const bdhtml = window.document.body.innerHTML
      let prnhtml = document.getElementById('qr').innerHTML
      window.document.body.innerHTML = prnhtml
      window.print()
      window.document.body.innerHTML = bdhtml
    },
    open_qr_popup () {
      this.qr_popup_seen = true
    },
    close_qr_popup () {
      this.qr_popup_seen = false
    },
    open_jia_cai_popup () {
      this.jia_cai_popup_seen = true
    },
    close_jia_cai_popup () {
      this.jiacai = false
    },
    open_change_desk_popup () {
      this.c_desk_popup_seen = true
    },
    close_change_desk_popup () {
      this.c_desk_popup_seen = false
    },
    open_jiesuan_popup () {
      this.jiesuan_popup_seen = true
    },
    close_jiesuan_popup () {
      this.jiesuan_popup_seen = false
    }
  }
}
</script>

