<template>
  <div class="view">
    <div class="view-container">
      <c-header/>
      <c-table @change="handleChangeNum"/>
      <c-pagination/>
      <c-detail v-show="pop_seen" @close="handleChangeNum"/>
    </div>
  </div>
</template>

<script>
import CHeader from './invoice/Header'
import CTable from './invoice/Table'
import CPagination from './invoice/Pagination'
import CDetail from './invoice/Detail'

export default {
  name: 'ProductSettings',
  data () {
    return {
      pop_seen: false
    }
  },
  components: {
    CHeader,
    CTable,
    CPagination,
    CDetail
  },
  methods: {
    handleChangeNum (seen) {
      // alert('click')
      // console.log('debug', seen)
      this.pop_seen = seen
      // console.log('this.seen : debug : ', this.pop_seen)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .view
    min-width 5.6rem
    pad1()
    .view-container
      pad2()
      min-height 50vw
      position relative
      box-sizing border-box
      // min-width 11.2rem
</style>



