<template>
  <div class="header">
    <div class="header-left">
      <h3>交易完成订单列表</h3>
    </div>
    <div class="header-right">
      <div class="search">
        <div><input type="text" class="search-input" placeholder="搜索"></div>
        <div class="search-btn">&#xe632;</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CHeader',
  methods: {
    handleClick () {
      this.$emit('change', true)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'

  .header
    space-between()
    height 1.4rem
    padding-top .2rem
    // background #ff0
    box-sizing border-box
    .header-left
      min-width 2rem
      font-size .36rem
      height .56rem
      line-height .56rem
      h3
        line-height 1rem
      .add-btn
        list-button()
    .header-right
      padding-top .2rem
      space-between()
      box-sizing border-box
      min-width 6rem
      .status
        mb028()
        width 3rem
        border 1px solid #aaf
        margin-right .28rem
      .search
        space-between()
        min-width 4rem
        box-sizing border-box
        .search-input
          mb028()
          background #eee
          height .56rem
          line-height .56rem
          min-width 5rem
          margin-right .14rem
          padding 0 .1rem
          border-radius .1rem
        .search-btn
          mb028()
          min-width .7rem
          height .56rem
          line-height .56rem
          font-family 'iconfont'
          font-size .40rem
          color #aaf
</style>



