<template>
  <div class="table-wrapper">
    <table class="table">
      <thead>
        <tr class="title">
          <th>序号</th>
          <th>标签名称</th>
          <th>排序</th>
          <th>显示</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>1</td>
          <td>不要葱</td>
          <td>1</td>
          <td>已显示</td>
          <td><span class="change-btn" @click="handleClick">修改</span><span class="change-btn" @click="handleClick">删除</span></td>
        </tr>
        <tr>
          <td>2</td>
          <td>微辣</td>
          <td>2</td>
          <td>已显示</td>
          <td><span class="change-btn" @click="handleClick">修改</span><span class="change-btn" @click="handleClick">删除</span></td>
        </tr>
        <tr>
          <td>3</td>
          <td>不要葱</td>
          <td>3</td>
          <td>已显示</td>
          <td><span class="change-btn" @click="handleClick">修改</span><span class="change-btn" @click="handleClick">删除</span></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>

export default {
  name: 'CTable',
  methods: {
    handleClick () {
      this.$emit('change', true)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .table-wrapper
    .table
      width 100%
      margin-bottom 1.6rem
      .title
        height .62rem
        line-height .62rem
        background #f2f2f2
        border 1px solid #f2f2f2
      th
        text-align center
      td
        text-align center
        padding .2rem
        border 1px solid #e4e4e4
        img
          margin-right .2rem
        .change-btn
          list-button()
          margin-left .32rem
          background #38a8fe
</style>



