<template>
  <div>
    <c-header :title="title" :options1="options1" :options2="options2" />
    <div class="container">
      <div class="top5">
        <i-echarts :option="option"/>
      </div>
      <div class="bottom5">
        <i-echarts :option="option"/>
      </div>
    </div>
  </div>
</template>

<script>
import CHeader from '../commons/Header'
import IEcharts from '../commons/I-Echarts'
export default {
  name: 'ItemNumber',
  components: {
    CHeader,
    IEcharts
  },
  data () {
    return {
      title: '单品排行（单品下单量）',
      options1: ['按年统计', '按月统计'],
      options2: [2018, 2017, 2016, 2015, 2014],
      option: {
        title: {
          text: 'ECharts 入门示例'
        },
        tooltip: {},
        // legend: {
        //   data: ['销量']
        // },
        yAxis: {
          type: 'category',
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          data: [
            {
              value: '红烧排骨',
              textStyle: {
                color: '#f00'
              }
            }, {
              value: '西红柿炒蛋',
              textStyle: {
                color: '#f00'
              }
            }, {
              value: '红烧排骨',
              textStyle: {
                color: '#f00'
              }
            }, {
              value: '西红柿炒蛋',
              textStyle: {
                color: '#f00'
              }
            }, {
              value: '西红柿炒蛋',
              textStyle: {
                color: '#f00'
              }
            }
          ]
          // data: ['红烧排骨', '西红柿炒蛋', '红烧排骨', '西红柿炒蛋', '西红柿炒蛋']
        },
        xAxis: {
          show: false
        },
        grid: {
          left: '15%',
          top: '25%'
        },
        series: [{
          name: '销量',
          type: 'bar',
          label: {
            normal: {
              show: true,
              position: 'insideRight',
              distance: -30,
              color: '#000'
            }
          },
          itemStyle: {
            normal: {
              // 随机显示
              // color: function (d) {return "#"+Math.floor(Math.random()*(256*256*256-1)).toString(16)}

              // 定制显示（按顺序）
              color: function (params) {
                var colorList = ['#C33531', '#EFE42A', '#64BD3D', '#EE9201', '#29AAE3']
                return colorList[params.dataIndex]
              }
            }
          },
          data: [5, 20, 36, 15, 10]
        }]
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'

  .container
    space-between()
    width 100%
    height 300px
    background #eee
    margin-bottom 0.8rem
    .top5
      width 45%
      height 100%
      padding 0 2%
      // background #ff00ff
    .bottom5
      width 45%
      height 100%
      padding 0 2%
      // background #ffff00
</style>



