<template>
  <div class="table-wrapper">
    <div class="header">
      <div class="title">
        历史数据
      </div>
    </div>
    <div class="body">
      <item-number></item-number>
      <quantity-amount></quantity-amount>
      <amount-line></amount-line>
    </div>

  </div>
</template>

<script>
import ItemNumber from './components/ItemNumber'
import QuantityAmount from './components/QuantityAmount'
import AmountLine from './components/AmountLine'
export default {
  name: 'HistoryStatistics',
  components: {
    ItemNumber,
    QuantityAmount,
    AmountLine
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .table-wrapper
    .header
      space-between()
      box-sizing border-box
      .title
        mb028()
        min-width 2rem
        font-size .36rem
        height .56rem
        line-height .56rem
    .table
      width 100%
      margin-bottom 1.6rem
      td
        text-align center
        padding .2rem
        border 1px solid #e4e4e4
        img
          margin-right .2rem
        .change-btn
          list-button()
          margin-left .32rem
          background #38a8fe
</style>



