<template>
  <div class="header">
    <div class="title">{{ title }}</div>
    <div class="header-right">
      <select name="" id="" class="status">
        <option value="1">{{ options1[0] }}</option>
        <option value="0">{{ options1[1] }}</option>
      </select>
      <div class="search">
        <div>
          <select name="" id="" class="status">
            <option v-for="(item, index) of options2" value="index" :key="index">{{ item }}</option>
          </select>
        </div>
        <div class="search-btn">&#xe632;</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CHeader',
  props: {
    title: {
      type: String
    },
    options1: {
      type: Array
    },
    options2: {
      type: Array
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'

  .header
    space-between()
    box-sizing border-box
    .title
      mb028()
      min-width 2rem
      font-size .36rem
      height .56rem
      line-height .56rem
    .header-right
      space-between()
      box-sizing border-box
      min-width 6rem
      .status
        mb028()
        width 3rem
        border 1px solid #aaf
        margin-right .28rem

      .search
        space-between()
        min-width 4rem
        box-sizing border-box
        .status
          mb028()
          background #eee
          height .56rem
          line-height .56rem
          min-width 3.66rem
          margin-right .14rem
          padding 0 .1rem
          border-radius .1rem
        .search-btn
          mb028()
          min-width .7rem
          height .56rem
          line-height .56rem
          font-family 'iconfont'
          font-size .40rem
          color #aaf
</style>



