<template>
  <div class="mask">
    <div class="popup">
      <div class="header">
        <span class="title">发票详情</span><span class="close" @click="closePopup">&#xe64e;</span>
      </div>
      <div class="main">
        <p>
          <span class="label">订单编号：</span><input type="text" class="input-text" value="20180304000001">
        </p>
        <p>
          <span class="label">发票类型：</span><input type="text" class="input-text" value="单位">
        </p>
        <p>
          <span class="label">发票金额：</span><input type="text" class="input-text" value="108元">
        </p>
        <p>
          <span class="label">发票抬头：</span><input type="text" class="input-text" value="公司">
        </p>
        <p>
          <span class="label">发票税号：</span><input type="text" class="input-text" value="234341324132">
        </p>
        <p>
          <span class="label">发票内容：</span><input type="text" class="input-text" value="餐饮">
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CDetail',
  methods: {
    closePopup () {
      this.$emit('close', false)
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .mask
    mask()
    h-v-center()
    top 1.04rem
    .popup
      width 11.2rem
      height 6.12rem
      background #fff
      .header
        popup-header-footer()
        .title
          float left
          text-indent .7rem
        .close
          font-family 'iconfont'
          padding-right .42rem
      .main
        h-v-center()
        flex-direction column
        height 4rem
        p
          line-height .6rem
          .label
            padding-right .4rem
          .input-text
            width 6rem
            border 1px solid #a9a9a9
            padding-left .1rem
      .footer
        popup-header-footer()
        padding-right .2rem
        .cancel
        .save
          list-button()
          margin-right .58rem
</style>



