<template>
  <div class="view">
    <div class="view-container">
      <div class="tab">
        <span :class="{all: true, 'tab-active': tab_active[0]}" @click="handleTabSwitch(0)">全 部</span>
        <span :class="{unpay: true, 'tab-active': tab_active[1]}" @click="handleTabSwitch(1)">待付款</span>
        <span :class="{paid: true, 'tab-active': tab_active[2]}" @click="handleTabSwitch(2)">交易完成</span>
      </div>
      <c-header @change="handleChangeNum"/>
      <c-table @change="handleChangeNum"/>
      <c-pagination/>
      <c-detail v-show="pop_seen" @close="handleChangeNum"/>
    </div>
  </div>
</template>

<script>
import CHeader from './order/Header'
import CTable from './order/Table'
import CPagination from './order/Pagination'
import CDetail from './order/Detail'

export default {
  name: 'OrderView',
  components: {
    CHeader,
    CTable,
    CPagination,
    CDetail
  },
  data () {
    return {
      pop_seen: false,
      type: 0 // 0 全部 1 待付款 2 交易完成
      // tab_active: [true, false, false]
    }
  },
  computed: {
    tab_active () {
      const active = [true, false, false]
      if (this.type === 0) {
        return active
      } else if (this.type === 1) {
        active.fill(false)
        active[this.type] = true
        return active
      } else {
        active.fill(false)
        active[this.type] = true
        return active
      }
    }
  },
  methods: {
    handleChangeNum (seen) {
      // alert('click')
      // console.log('debug', seen)
      this.pop_seen = seen
      // console.log('this.seen : debug : ', this.pop_seen)
    },
    handleTabSwitch (type) {
      this.type = type
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/viewpadding.styl'
  .view
    min-width 5.6rem
    pad1()
    .view-container
      pad2()
      min-height 50vw
      position relative
      box-sizing border-box
      .tab
        width 6.8rem
        height 1rem
        line-height 1rem
        font-size .36rem
        // background #ff0
        display flex
        justify-content space-between
        span
          width 1.8rem
          // background #0ff
          text-align center
        .tab-active
          border-bottom .1rem solid #ff4c1b
          color #ff4c1b
</style>



